import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userCheck: true,
    show: true,
    version: 'YH-W-1.0.0', // 
    agentNo: '2800001', // 
    active: 0,
    token: [],
    password: '',
    imgArray: [],
    level: {
      1: '',
      2: '',
      3: '',
      4: '',
      5: '',
      6: '',
      7: ''
    },
    upgrade: {
      1: '',
      2: ''
    },
    userList: [],
    moneyLimit: {},
    openId: '',
    phone: false,
    real: 0
  },
  mutations: {
    SET_USERCHECK (state, code) {
      state.userCheck = code
    },
    SET_TOKEN (state, token) {
      state.token = token
    },
    SET_IMG (state, imgArray) {
      state.imgArray = imgArray
    },
    Change_Password (state, code) {
      state.password = code
    },
    Change_Limit (state, code) {
      state.moneyLimit = code
    },
    SET_OPENID (state, code) {
      state.openId = code
    },
    IS_PHONE (state, code) {
      state.phone = code
    },
    SET_REAL (state, code) {
      state.real = code
    }
  },
  actions: {
    SetUserCheck ({
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_USERCHECK', code)
      })
    },
    SetToken ({ // token
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_TOKEN', code)
      })
    },
    SetReal ({ // token
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_REAL', code)
      })
    },
    SetPassword ({ // 
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('Change_Password', code)
      })
    },
    setMoney ({
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('Change_Limit', code)
      })
    },
    setOpenid ({
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('SET_OPENID', code)
      })
    },
    setPhone ({
      commit,
      state
    }, code) {
      return new Promise((resolve, reject) => {
        commit('IS_PHONE', code)
      })
    }
  },
  plugins: [createPersistedState({
    storage: window.localStorage
  })]

})
