

































































































































import Vue from 'vue'
import axios from 'axios'

let wx = require('weixin-js-sdk')

export default Vue.extend({
  data () {
    return {
      routeData: '',
      merchantNo: '',
      version: '',
      agentNo: '',
      phone: '',
      freezeStatus: ''
    }
  },
  watch: {
    '$route' (to, from) {
      let vm = (this as any)
      vm.routeData = to.name
      vm.merchantNo = ''
      if (to.name === 'ifarme') {
        console.log(vm.$route.query.title)
        to.meta.title = vm.$route.query.title
        document.body.title = vm.$route.query.title
      }
      new Promise((resolve, reject) => { //  tokenmerchantNo
        vm.$stact.state.token.length !== 0 ? vm.merchantNo = JSON.parse(vm.$stact.state.token)[0].merchantNo : console.log('')
        resolve()
      }).then(r => {
        if (vm.merchantNo) { // merchantNo   
          if (vm.routeData === 'login' || vm.routeData === 'forget' || vm.routeData === 'registered') {
            vm.$router.push({ name: 'home' })
          } else {
            vm.freezeStatus = JSON.parse(vm.$stact.state.token)[0].freezeStatus
            if (vm.routeData !== 'real' && vm.routeData !== 'realcard') {
              if (vm.freezeStatus !== '10B') {
                vm.$confirm('', '', {
                  confirmButtonText: '',
                  cancelButtonText: '',
                  type: 'warning'
                }).then(() => {
                  vm.$router.push({ name: 'real' })
                }).catch(() => {
                })
              }
            }
          }
        } else {
          if (vm.routeData === 'login' || vm.routeData === 'forget' || vm.routeData === 'registered' || vm.routeData === 'ifarme') {
          } else {
            vm.$router.push({ name: 'login' })
          }
        }
      })
        .catch(error => console.log(error))
    }
  },
  created () {
    let vm = (this as any)
    vm.version = vm.$stact.state.version
    vm.agentNo = vm.$stact.state.agentNo
    let shurl: string = ''
    if (vm.$stact.state.token.length !== 0) {
      vm.phone = JSON.parse(vm.$stact.state.token)[0].phone
      shurl = 'http://yihuan.llyzf.cn/lly-posp-proxy/return/return.html#/registered?invite=' + vm.phone
    } else {
      shurl = 'http://yihuan.llyzf.cn/lly-posp-proxy/return/return.html#/registered'
    }
    vm.wxShare(shurl)
  },
  mounted () {
  },
  methods: {
    goTop () {
      this.$router.go(-1)
    },
    wxShare (shurl: string) { // 
      let vm: any = this
      let url = location.href.split('#')[0]
      let parmas = {
        'agentNo': vm.agentNo,
        'url': url,
        'version': vm.version
      }
      let config = {
        headers: {
          'Content-Type': 'application/json; charset=utf-8'
        }
      }
      const root = process.env.NODE_ENV === 'production' ? 'http://yihuan.llyzf.cn/lly-posp-proxy/' : '/apis'
      axios.post(`${root}app/getWxJs.app`, parmas, config)
        .then((res: any) => {
          wx.config({
            debug: false, // ,apialertpclogpc
            appId: res.data.appId, // corpid
            timestamp: res.data.timestamp, // 
            nonceStr: res.data.nonceStr, // 
            signature: res.data.signature, // 1
            jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline', 'onMenuShareQQ'] // JSJS2
          })
          try {
            wx.ready(function () {
              wx.onMenuShareAppMessage({
                title: '', // 
                desc: '', // 
                link: shurl, // JS
                imgUrl: 'http://yihuan.llyzf.cn/lly-posp-proxy/one.png' // 
              }, function (res: any) {
              })
              wx.onMenuShareTimeline({
                title: '', // 
                link: shurl, // JS
                imgUrl: 'http://yihuan.llyzf.cn/lly-posp-proxy/one.png', // 
                success: function (res: any) {
                }
              })
              wx.onMenuShareQQ({
                title: '', // 
                desc: '', // 
                link: shurl, // JS
                imgUrl: 'http://yihuan.llyzf.cn/lly-posp-proxy/one.png' // 
              }, function (res: any) {
              })
            })
          } catch (e) {
          }
        })
        .catch((err: any) => {
          console.log(err)
        })
    }
  }
})
